/* Estilos para o botão do WhatsApp */
.btn-whatsapp {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #25d366; /* Cor do fundo do WhatsApp */
  color: white;
  border-radius: 50%;
  padding: 10px;
  margin: 0 5px;
  cursor: pointer;
  transition: all 0.3s ease; /* Transição suave para hover */
  text-decoration: none; /* Remove o sublinhado do link */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Sombra suave */
}

.btn-whatsapp:hover {
  background-color: #128c7e; /* Cor do fundo no hover */
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15); /* Sombra mais forte no hover */
  transform: translateY(-2px); /* Leve elevação ao passar o mouse */
}

.btn-whatsapp svg {
  width: 15px; /* Tamanho do ícone */
  height: 15px; /* Tamanho do ícone */
}

.btn-whatsapp:active {
  transform: translateY(2px); /* Efeito de clique */
}

/* Efeito de foco acessível */
.btn-whatsapp:focus {
  outline: none; /* Remove o contorno padrão */
  box-shadow: 0 0 0 3px rgba(37, 211, 102, 0.5); /* Destaque ao focar */
}


