/* Estilos para o componente Chacaras */
.chacaras-container {
    max-width: 1200px;
    margin: auto;
    margin-top: 50px;
    margin-bottom: 50px;
    padding: 20px;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .chacaras-container h2 {
    font-size: 2rem;
    color: #2c3e50;
    margin-bottom: 20px;
    text-align: center;
    font-weight: 600;
  }
  
  .chacaras-container p {
    font-size: 1rem;
    color: #fff;
    text-align: center;
    margin: 10px 0;
  }
  
  .chacaras-container ul {
    list-style-type: none;
    padding: 0;
  }
  
  .chacaras-container li {
    padding: 15px;
    margin-bottom: 10px;
    background-color: #ecf0f1;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease, transform 0.3s ease;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .chacaras-container li:hover {
    background-color: #bdc3c7;
    transform: translateY(-5px);
  }
  
  .chacaras-container li span {
    font-weight: bold;
  }
  
  .chacaras-container li p {
    font-size: 0.9rem;
    color: #7f8c8d;
  }
  
  .chacaras-container p.empty-message {
    font-size: 1rem;
    color: #95a5a6;
    text-align: center;
    font-style: italic;
  }
  
  .btn-delete {
    background-color: #e74c3c;
    color: white;
    border: none;
    padding: 8px 15px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .btn-delete:hover {
    background-color: #c0392b;
  }

  /* Modal Styles */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: rgb(255, 255, 255);
  padding: 20px;
  border-radius: 8px;
  width: 400px;
}

.modal-actions {
  display: flex;
  justify-content: space-between;
}

.modal-actions button {
  padding: 10px 20px;
  border: none;
  cursor: pointer;
}

.btn-save {
  background-color: #1a3950;
  color: white;
}

.btn-close {
  background-color: red;
  color: white;
}

  