.visitante-detalhes-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 50px;
    margin-bottom: 50px;
    }
  
  .titulo {
    text-align: center;
    font-size: 2rem;
    color: #333;
    margin-bottom: 20px;
  }
  
  .visitante-detalhes {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  }
  
  .detalhes-item {
    font-size: 1.1rem;
    color: #333;
    margin-bottom: 10px;
  }
  
  .detalhes-item strong {
    font-weight: bold;
    color: #007bff;
  }
  
  .loading {
    text-align: center;
    font-size: 1.2rem;
    color: #888;
  }
  
  .btn-back {
    display: block;
    width: 200px;
    margin: 30px auto;
    padding: 10px 15px;
    background-color: #007bff;
    color: #fff;
    font-size: 1.1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .btn-back:hover {
    background-color: #0056b3;
  }
  
  .message {
    color: red;
    font-size: 1.1rem;
    margin-top: 20px;
    text-align: center;
  }

  /* Estilos para o botão de atualizar */
.btn-update {
  background-color: var(--cor0); /* Cor verde */
  color: var(--cor3); /* Cor do texto */
  border: none; /* Remover borda */
  padding: 10px 20px; /* Espaçamento interno */
  font-size: 16px; /* Tamanho da fonte */
  cursor: pointer; /* Cursor em forma de mão */
  border-radius: 5px; /* Bordas arredondadas */
  transition: background-color 0.3s ease; /* Efeito de transição suave */
}

.btn-update:hover {
  background-color: var(--cor3);
  color: var(--cor0);
}

.btn-update:active {
  background-color: #388e3c; /* Cor ainda mais escura ao clicar */
}

.btn-container {
  margin-top: 20px; /* Espaçamento superior */
}

  