/* Estilos para a mensagem */
.message-container {
    position: fixed;
    top: 70px; /* Ajuste a altura para aparecer abaixo do navbar */
    right: 20px;
    background-color: #fff;
    color: white;
    padding: 10px 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    font-size: 16px;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 250px;
    max-width: 400px;
    transition: opacity 0.3s ease-out;
  }
  
  /* Estilos para o tipo de mensagem */
  .message-container.success {
    background-color: #28a745; /* Verde para sucesso */
  }
  
  .message-container.error {
    background-color: #dc3545; /* Vermelho para erro */
  }
  
  /* Estilos para o botão de fechar */
  .close-btn {
    background: none;
    border: none;
    color: white;
    font-size: 20px;
    cursor: pointer;
    padding: 0;
    margin-left: 10px;
  }
  
  .close-btn:hover {
    color: #ddd;
  }
  
  /* Ajuste no texto */
  .message-container p {
    margin: 0;
    flex-grow: 1;
  }
  