/* Escopo específico para o container do perfil */
.profile-container {
  font-family: 'Arial', sans-serif;
  background-color: #ffffff;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 20px 30px;
  width: 100%;
  max-width: 600px;
  text-align: center;
  margin: auto;
  margin-top: 50px;
  margin-bottom: 50px;
}

/* Cabeçalho */
.profile-container h2 {
  margin-bottom: 20px;
  font-size: 24px;
  color: #333333;
  display: inline-block;
  padding-bottom: 5px;
}

/* Mensagem */
.profile-container .message-container {
  margin-bottom: 15px;
}

/* Informação do Perfil */
.profile-container .profile-info {
  text-align: left;
  margin-top: 20px;

}

.profile-container .profile-info p {
  margin: 10px 0;
  font-size: 16px;
  color: #555555;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  padding: 20px 20px 35px 20px;
  height: 30px;

}

.profile-container .profile-info p strong {
  color: #333333;
}

/* Botões */
.profile-container button {
  background-color: #1a3950;
  color: #ffffff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  margin: 10px 5px;
  transition: background-color 0.3s ease;
}

.profile-container button:hover {
  background-color: var(--cor3);
  color: var(--cor0);
}

.profile-container button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

/* Inputs e Select */
.profile-container input,
.profile-container select {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #cccccc;
  border-radius: 5px;
  font-size: 16px;
  color: #333333;
}

.profile-container input:focus,
.profile-container select:focus {
  border-color: #6c63ff;
  outline: none;
  box-shadow: 0 0 5px rgba(108, 99, 255, 0.5);
}

/* Responsividade */
@media (max-width: 768px) {
  .profile-container {
    padding: 15px 20px;
  }

  .profile-container button {
    width: 100%;
  }
}
